<template>
    <div class="charging-pile-container">
        <!-- 页面标题 -->
        <div class="page-header">
            <h2>充电桩管理系统</h2>
            <el-button type="primary" icon="el-icon-plus" @click="handleAdd">新增充电桩</el-button>
        </div>

        <!-- 统计数据概览 -->
        <div class="stat-cards">
            <el-row :gutter="10">
                <el-col :span="6" v-for="item in statData" :key="item.title">
                    <el-card class="stat-card tech-card" shadow="hover">
                        <div class="stat-content">
                            <div class="stat-icon" :style="{ backgroundColor: item.color + '33', border: '1px solid ' + item.color }">
                                <i :class="item.icon"></i>
                            </div>
                            <div class="stat-info">
                                <div class="stat-value">{{ item.value }}<span class="unit">{{ item.unit }}</span></div>
                                <div class="stat-title">{{ item.title }}</div>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
        </div>

        <!-- 充电桩列表展示区域 -->
        <div class="table-section">
            <el-card shadow="hover" class="table-card tech-card">
                <div slot="header" class="data-header">
                    <img src="@/assets/楼图标.png" alt="楼宇图标" class="card-icon-bottom" />
                    <span class="card-text-bottom">充电桩列表</span>
                </div>
                <!-- 搜索和筛选区域 -->
                <div class="filter-section inside-table">
                    <el-form :inline="true" :model="searchForm" class="demo-form-inline search-form">
                        <el-form-item label="充电桩编号">
                            <el-input v-model="searchForm.code" placeholder="请输入编号" clearable></el-input>
                        </el-form-item>
                        <el-form-item label="状态">
                            <el-select v-model="searchForm.status" placeholder="请选择状态" clearable>
                                <el-option label="全部" value=""></el-option>
                                <el-option label="空闲" value="idle"></el-option>
                                <el-option label="充电中" value="charging"></el-option>
                                <el-option label="故障" value="fault"></el-option>
                                <el-option label="离线" value="offline"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="位置">
                            <el-input v-model="searchForm.location" placeholder="请输入位置" clearable></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" icon="el-icon-search" @click="handleSearch">查询</el-button>
                            <el-button icon="el-icon-refresh" @click="handleReset">重置</el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="table-wrapper">
                    <el-table :data="chargingPileList" style="width: 100%" border v-loading="loading"
                        height="100%">
                        <el-table-column prop="code" label="充电桩编号" align="center"></el-table-column>
                        <el-table-column prop="name" label="名称" align="center"></el-table-column>
                        <el-table-column prop="location" label="位置" align="center"></el-table-column>
                        <el-table-column prop="power" label="功率(kW)" align="center"></el-table-column>
                        <el-table-column prop="status" label="状态" align="center">
                            <template slot-scope="scope">
                                <el-tag :type="getStatusType(scope.row.status)">{{ getStatusText(scope.row.status)
                                    }}</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="lastUseTime" label="最后使用时间" align="center"></el-table-column>
                        <el-table-column label="操作" align="center" width="200">
                            <template slot-scope="scope">
                                <div class="operation-buttons">
                                    <el-button size="mini" @click="handleDetail(scope.row)">详情</el-button>
                                    <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                                    <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                </div> 
            </el-card>
        </div>

        <!-- 新增/编辑充电桩对话框 -->
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="500px" @close="handleDialogClose">
            <el-form :model="editForm" :rules="editRules" ref="editForm" label-width="100px">
                <el-form-item label="充电桩编号" prop="code">
                    <el-input v-model="editForm.code" :disabled="isEdit"></el-input>
                </el-form-item>
                <el-form-item label="名称" prop="name">
                    <el-input v-model="editForm.name"></el-input>
                </el-form-item>
                <el-form-item label="位置" prop="location">
                    <el-input v-model="editForm.location"></el-input>
                </el-form-item>
                <el-form-item label="功率(kW)" prop="power">
                    <el-input-number v-model="editForm.power" :min="1" :max="1000"></el-input-number>
                </el-form-item>
                <el-form-item label="状态" prop="status">
