                        <el-option label="空闲" value="idle"></el-option>
                        <el-option label="充电中" value="charging"></el-option>
                        <el-option label="故障" value="fault"></el-option>
                        <el-option label="离线" value="offline"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitForm">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 充电桩详情对话框 -->
        <el-dialog title="充电桩详情" :visible.sync="detailDialogVisible" width="600px">
            <el-form label-width="120px" v-if="currentPile">
