                    <span>{{ currentPile.code }}</span>
                </el-form-item>
                <el-form-item label="名称：">
                    <span>{{ currentPile.name }}</span>
                </el-form-item>
                <el-form-item label="位置：">
                    <span>{{ currentPile.location }}</span>
                </el-form-item>
                <el-form-item label="功率：">
                    <span>{{ currentPile.power }} kW</span>
                </el-form-item>
                <el-form-item label="状态：">
                    <el-tag :type="getStatusType(currentPile.status)">{{ getStatusText(currentPile.status) }}</el-tag>
                </el-form-item>
                <el-form-item label="最后使用时间：">
                    <span>{{ currentPile.lastUseTime }}</span>
                </el-form-item>
                <el-form-item label="累计充电次数：">
                    <span>{{ currentPile.chargeCount }}</span>
                </el-form-item>
                <el-form-item label="累计充电量：">
                    <span>{{ currentPile.totalPower }} kWh</span>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="detailDialogVisible = false">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>

