            this.$message.success('重置成功');
        },
        handleAdd() {
            this.isEdit = false;
            this.dialogTitle = '新增充电桩';
            this.editForm = {
                id: '',
                code: '',
                name: '',
                location: '',
                power: 60,
                status: 'idle'
            };
            this.dialogVisible = true;
        },
