                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // 模拟删除操作
                this.$message.success('删除成功');
            }).catch(() => {
                this.$message.info('已取消删除');
            });
        },
        handleDetail(row) {
            this.currentPile = row;
            this.detailDialogVisible = true;
        },
        submitForm() {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
