<style scoped>
.charging-pile-container {
  padding: 10px;
  height: calc(100vh - 20px);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background: url('../../assets/1.gif') center center no-repeat;
  background-size: cover;
  color: #e0e0ff;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  position: relative;
  overflow: auto;
}

.charging-pile-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(10, 25, 41, 0.85);
  z-index: -1;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.page-header h2 {
  margin: 0;
  color: #a3cfff;
  text-shadow: 0 0 8px rgba(79, 195, 247, 0.7);
}

.stat-cards {
  margin-bottom: 10px;
}

.stat-card {
  border: none;
  margin-bottom: 10px;
  background: linear-gradient(145deg, rgba(12, 33, 58, 0.9), rgba(10, 30, 55, 0.9));
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(26, 54, 93, 0.7);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  height: 100px;
  display: flex;
  align-items: center;
  z-index: 10;
}

.stat-card::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    to bottom right,
    rgba(26, 107, 194, 0.1),
    transparent,
    transparent,
    transparent
  );
  transform: rotate(30deg);
  transition: all 0.6s ease;
  z-index: -1;
}

.stat-card:hover::before {
  transform: rotate(30deg) translate(20%, 20%);
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(77, 171, 247, 0.8);
  z-index: 20;
}

.stat-content {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.stat-icon {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
  flex-shrink: 0;
  background: rgba(64, 158, 255, 0.2);
  border: 1px solid #409EFF;
}

.stat-icon i {
  font-size: 20px;
  color: #409EFF;
  text-shadow: 0 0 5px rgba(64, 158, 255, 0.7);
}

.stat-info {
  flex: 1;
  text-align: left;
  overflow: hidden;
}

.stat-value {
  font-size: 18px;
  font-weight: bold;
  color: #a3cfff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-shadow: 0 0 8px rgba(77, 171, 247, 0.7);
  margin-bottom: 3px;
}

.unit {
  font-size: 12px;
  margin-left: 2px;
  color: #81d4fa;
}

.stat-title {
  font-size: 12px;
  color: #79b1ff;
  margin: 2px 0;
}

.filter-section.inside-table {
  margin-bottom: 0;
  background: rgba(10, 30, 55, 0.5);
  padding: 15px;
  border-bottom: 1px solid rgba(26, 54, 93, 0.7);
}

.search-form {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  gap: 15px;
}

.search-form ::v-deep .el-form-item {
  margin-bottom: 0;
}

.search-form ::v-deep .el-form-item__label {
  color: #ffffff;
  font-size: 14px;
}

.search-form ::v-deep .el-input__inner {
