  color: #a3cfff;
  font-size: 16px;
  background: linear-gradient(to right, rgba(10, 30, 55, 0.9), rgba(12, 37, 69, 0.9));
  padding: 10px !important;
  border-bottom: 1px solid rgba(26, 54, 93, 0.7);
  position: relative;
  display: flex;
  align-items: center;
}

